.menu{flex-direction:column;background-color:var(--color-black)}
.menu-title{margin-bottom:2rem;text-align: center;}
.menu-menu{
    width:100%;
    margin:2rem 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
}
.menu_heading{
    font-family:var(--font-base);
    font-weight:600;
    font-size:45px;
    line-height:58.5px;
    color:var(--color-white);
    letter-spacing:0.04em;
}
.menu_juice,.menu_cocktails{
    flex:1;
    width:100%;
    flex-direction: column;
}
.menu_img{
    width: 410px;
    margin: 0 2rem;
}
.menu_img img{
    width: 100%;
    height: auto;
}
.menu_items{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 2rem 0;
}
@media screen and (min-width:2000px){.menu_img{width: 650px}.menu_img img{height:920px}}
@media screen and (max-width:1150px){.menu-menu{flex-direction:column;align-items: center;width:100%}}
@media screen and (max-width:650px){.menu_img{width:100%}.menu_heading{font-size:35px;line-height:48.5px}.menu_cocktails{margin-top:50px;}}





