.menuitem{
    width:100%;
    margin:1rem 0;
    display: flex;
    flex-direction:column;
}
.menuitem-head{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.menuitem-sub{width:100%;margin-top:0.2rem}
.menuitem-name{flex:1}
.menuitem-dash{
    width:90px;
    height:1px;
    background-color:var(--color-golden);
    margin:0 1rem
}

.menuitem-price{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}



