.gallery{
    flex-direction: row;
    background-color: var(--color-black);
    padding: 4rem 0 4rem 6rem;
}
.gallery-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: 500px;
    padding-right:2rem;
}
.gallery-content button{margin-top:1rem}
.gallery-images{
    flex: 1;
    display: flex;
    flex-direction: row;
    max-width: 50%;
    position: relative;
}
.images-container{
    display: flex;
    flex-direction: row;
    width:max-content;
    overflow-x: scroll;
    -ms-overflow-style: none;
    -webkit-overflow-style: none;
    scrollbar-width: none;
}
.images-container::-webkit-scrollbar{display: none}
.image-card{min-width:301px;position: relative;height: 447px;margin-right:2rem}
.image-icon{
    position: absolute;
    color: #fff;
    font-size: 2rem;
    opacity:0;
    cursor: pointer;
    transition:.5s ease;
    -webkit-transition:.5s ease;
    -moz-transition:.5s ease;
    -ms-transition:.5s ease;
    -o-transition:.5s ease;
}
.image-card img{
    width: 100%;
    height:100%;
    object-fit: cover;
    opacity: 1;
    transition:.5s ease;
    -webkit-transition:.5s ease;
    -moz-transition:.5s ease;
    -ms-transition:.5s ease;
    -o-transition:.5s ease;
}
.image-card:hover img{opacity:.35}
.image-card:hover .image-icon{opacity:1}
.images-arrow{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    position:absolute;
    bottom:5%;
}
.arrow-icon{
    color:var(--color-golden);
    cursor: pointer;
    font-size:2rem;
    background-color:var(--color-black);
    border-radius:5px;
    -webkit-border-radius:5px;
    -moz-border-radius:5px;
    -ms-border-radius:5px;
    -o-border-radius:5px;
}
.arrow-icon:hover{color:var(--color-white)}
@media screen and (min-width:2000px){
    .gallery-content button{margin-top:2rem}
    .gallery-content{min-width:1000px;padding-right:4rem}
    .image-card{min-width:400px;height:547px}
}
@media screen and (max-width:1150px){
    .gallery{flex-direction:column}
    .gallery-images{max-width:100%;margin:5rem 0}
}
@media screen and (max-width:850px){.gallery{padding:4rem 0 4rem 4rem}}
@media screen and (max-width:650px){
    .gallery{padding:4rem 0 4rem 4rem}
    .gallery-content{min-width:100%}
    .image-card{min-width:240px;height:320px}
}