.awards{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin-top:3rem;
}
.awards-card{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 1rem;
    min-width: 230px;
}
.awards-card img{
    width: 50px;
    height: 50px;
}
.card-content{
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
}
@media screen and (min-width:1900px){.awards-card{min-width: 390px}}
@media screen and (max-width:450px){
    .awards-card{
        min-width: 100%;
        margin: 1rem 0;
    }
}