nav{
    width: 100%;
    padding:1rem 2rem;
    background-color:var(--color-black);
    display: flex;
    justify-content: space-between;
    align-items: center;
    color:var(--color-white);
}
.nav-logo{
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav-logo img{
    width: 150px;
}
.nav-links{
    flex: 1;
    list-style:none;
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav-links li{margin: 0 1rem;cursor: pointer;text-transform: capitalize;}
.nav-links li:hover a{color:var(--color-grey) !important}
.login{display: flex;align-items: center;justify-content: flex-end;}
.login a{ text-decoration:none; margin:0 1rem; transition:0.5s ease; -webkit-transition:0.5s ease; -moz-transition:0.5s ease; -ms-transition:0.5s ease; -o-transition:0.5s ease; }
.login a:hover{border-bottom:1px solid var(--color-golden)}
.login div{width:1px;height: 30px;background-color:var(--color-grey);}


.menuu{cursor: pointer}
.menuu:hover{color:var(--color-golden) !important}
.nav-ss{display: none}
.nav-overlay{
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100vh;
    background-color:var(--color-black);
    transition:0.5s ease;
    -webkit-transition:0.5s ease;
    -moz-transition:0.5s ease;
    -ms-transition:0.5s ease;
    -o-transition:0.5s ease;
}
.nav-overlay .overlay-close{
    font-size:27px;
    color:var(--color-golden);
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
}
.nav-links-ss{list-style:none}
.nav-links-ss li{
    margin: 2rem;
    cursor: pointer;
    text-transform: capitalize;
    color:var(--color-golden);
    font-size:2rem;
    text-align: center;
    font-family:var(--font-base);
}
.nav-links-ss li:hover{color:var(--color-white)}

@media screen and (min-width:2000px){.nav-logo img{width:210px}}

@media screen and (max-width:1150px){.nav-links{display: none}.nav-ss{display: flex}}
@media screen and (max-width:650px){nav{padding:1rem}.login{display: none}.nav-logo img{width:110px}}
