.about{position:relative}
.about-overlay{position:absolute;inset:0}
.about-overlay img{width: 391px;height: 415px;z-index: 0}
.about-content{width:100%;z-index:2}
.about-content p{margin:2rem 0;color:var(--color-grey)}
.content_about{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    text-align:right;
}
.content_history{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align:left;
}
.content_knife{margin:2rem 4rem}
.content_knife img{height:910px}

@media screen and (min-width:2000px){.content_knife img{height:1110px}.about-content p{margin:4rem 0}}
@media screen and (max-width:900px){.about-content{flex-direction:column}.content_knife{margin:4rem 0}}
@media screen and (max-width:650px){.about-overlay img{width: 80%;height: 320px}}

