.vid{height:100vh;position: relative}
.vid video{width:100%;height:100%;object-fit:cover}
.vid-overlay{position:absolute;inset:0;background-color:rgba(0,0,0,0.65)}
.vid-overlay-circle{ width:100px; height:100px; border-radius:50%; -webkit-border-radius:50%; -moz-border-radius:50%; -ms-border-radius:50%; -o-border-radius:50%; border:1px solid var(--color-golden);cursor: pointer;}